<template>
<div class="index">
  <form action="/" class="from">
    <Search v-model="SearchValue"  placeholder="请输入搜索关键词" @search="onSearch" @cancel="onCancel" />
  </form>
    <aplayer autoplay  ref="music" :music="{
      title: musicTitle,
      author: musicAuthor,
      url:musicURL,
      pic:musicPIC,
       
    }">
    </aplayer>
  <list
      v-model="DataLoading"
      :offset="60"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
   >
       <Row class="Rows" v-for="item in listData" :key="item.id">
          <Col span="5"><img style=" vertical-align: middle;  border-radius: 50%;  width: 40px; height: 40px;"  :src="item.musicPIC" alt="" /> </Col> 
          <Col span="13" style="text-align:left;padding-left:10px;">{{item.musicTitle}}</Col>
     
          <Col span="4" style="text-align:right; overflow: hidden;">
             <!-- <Icon name="play" style="margin-top:7px"  size="30px" /> -->
             <Button plain  size="mini" type="primary" @click="plays(item)">播放</Button>
          </Col>
      </Row>
</list>

  
  <!-- <Button type="primary" size="mini">主要按钮</Button>
  <Button plain type="primary" size="small">朴素按钮</Button> -->
</div>
</template>

<script>
import Aplayer from 'vue-aplayer'
import data from '../../../../assets/json/index.json'
import {
  Button,
  Row, Col,Icon,
  Search,
  Cell, CellGroup,tag ,list 
} from 'vant';
export default {
  data() {
    return {
      SearchValue: "",
      DataLoading:false,
      finished:false,
      listData:[],
      list:15,
      musicTitle:'',
      musicAuthor:'',
      musicURL:"",
      musicPIC:""
    }
  },
  methods: {
    onSearch() { //确认搜索
      if (this.SearchValue == "") {
        return false;
      } else {
        console.log("确认")
      }

    },
    onCancel() { //取消搜索
      this.SearchValue = "";
    },
    onLoad(){
       setTimeout(() => {
        
        if(this.list==30){
          this.finished=true
          this.DataLoading=false 
        }else {
           this.list=this.list+5;
           this.DataLoading=false 
           //  this.finished=true
        }
       }, 3000);

    },
    plays(i){
      var this_=this
    //  this.musicURL="https://github.com/sunzhongt/imgData/blob/master/music/%E5%83%8F%E5%A4%AA%E9%98%B3%E4%B8%80%E6%A0%B7%E9%97%AA%E8%80%80.mp3?raw=true";
    //  this.musicPIC="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1545107460310&di=ca53d0ffcbc9c2449f75c3621a2e5cff&imgtype=0&src=http%3A%2F%2Fpic24.nipic.com%2F20121017%2F5955207_174906734000_2.jpg"
    //   var musics = document.getElementsByTagName("audio")[0] ;
       this.musicTitle=i.musicTitle
        this.musicAuthor=i.musicAuthor
        this.musicURL=i.musicURL
        this.musicPIC=i.musicPIC
    
      window.setTimeout(function(){
        this_.$refs.music.play()
      },1000)
       
    },
    play(){

    }
  },
  created(){
  
   
    
  },
  mounted() {
    var this_ =this
      this.listData=data;
    var dataOne=data[0]
    this.musicTitle=dataOne.musicTitle
    this.musicAuthor=dataOne.musicAuthor
    this.musicURL=dataOne.musicURL
    this.musicPIC=dataOne.musicPIC
   window.setTimeout(function(){
        this_.$refs.music.play()
    },1000)
  },

  components: {
    Button,
    Aplayer,
    Search,
     Cell, CellGroup,tag ,list,Row, Col,Icon
  }
}
</script>

<style lang="less" >
.index{
  position: relative;
  padding-top: 42px;
}
   .from{
      position: fixed;
      top: 45px;
      z-index: 1;
      width: 100%;
   }
  .Rows{

    // border: 1px #000 solid;
    color:rgb(255, 255, 255);
    box-shadow: 0px 1px 1px rgb(214, 209, 209);
    height: 40px;
    line-height: 40px;
    padding:10px 0px ;
    text-align: center;
    width: 95%;
    margin: 7px auto;
    border-radius: 7px;

  background: -webkit-linear-gradient(left top, rgb(248, 246, 246) , rgb(120, 209, 236)); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(bottom right,  rgb(248, 246, 246) , rgb(120, 209, 236)); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(bottom right,  rgb(248, 246, 246) , rgb(120, 209, 236)); /* Firefox 3.6 - 15 */
  background: linear-gradient(to bottom right,  rgb(248, 246, 246) , rgb(120, 209, 236)); /* 标准的语法 */
    img{
          // border: 1px #000 solid;
          vertical-align: middle;
       
          border-radius: 50%;
          width: 40px;
          height: 40px;
          // margin-left: 10px;
    }
  }
</style>




