<template>
<div class="home">
  <NavBar title="赞美" :style="{backgroundColor:'#000',color:'#fff'}" :fixed="true">
    <icon name="search" color="#fff" slot="right" />
  </NavBar>
 
  <router-view></router-view>

  <Tabbar v-model="active" :fixed="true"  @change="onChange">
    <Tabbar-item icon="wap-home" to="/home/index">赞美</Tabbar-item>
    <Tabbar-item icon="contact"  to="/home/user">我的</Tabbar-item>

  </Tabbar>
</div>
</template>

<script>
import {
  NavBar,
  icon,
  Tabbar,
  TabbarItem
} from 'vant';
export default {
  data() {
    return {
     active:0
    }
  },
  methods:{
    onChange(){

    }
  }
  ,components: {
    NavBar,
    icon,
    Tabbar,
    TabbarItem
  }
}
</script>

<style lang="less" scoped>
.home {
  padding-top: 50px;
  padding-bottom: 40px;
}
</style>




