import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home/home'


import homeRouter from '../components/router/homeRouter.js'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/home'
    },    
    {
      path: '/home',
      name: 'home',
      component: home,
      children:[
         // 子路由 文件 数组解放
        ...homeRouter
      ]
    }
  ]
})


