import index from '@/components/home/main/index/index';
import user from '@/components/home/main/user/user';
export default [
    {
        path: '/',
        redirect:'/home/index'
    },
    {
        path: '/home/index',
        name: '/home/index',
        component: index
    },
    {
        path: '/home/user',
        name: '/home/user',
        component: user
    }
]


